.sp-birthday {
    padding-top: 0;
    overflow: hidden; }

.sp-birthday-slider {
    .slider-circles-container {
        display: none; }

    .slider-arrow {
        &-prev {
            left: -90px; }

        &-next {
            right: -90px; } }

    .slider-arrow-container {
        position: absolute;
        left: 50%;
        bottom: -100px;
        transform: translateX(-50%);
        display: flex;
        align-items: center; }

    .slider-arrow {
        display: block;
        width: 52px;
        height: 52px;
        border-radius: 50%;
        position: relative;
        left: 0;
        right: 0;
        font-size: 25px;
        line-height: 56px;
        color: $base-c;
        background-color: $light-c;
        opacity: 0.8;
        box-shadow: 0 8px 16px #e1ecf7;

        &:hover {
            box-shadow: 0 16px 24px #cfdfef; } }

    .slide-nav-counter {
        display: block;
        width: 100px;
        margin-right: 30px;
        margin-left: 30px;
        font-size: 24px;
        line-height: 56px;
        color: $base-c; }

    img {
        min-height: 100%; } }

.sp-birthday-header {
    position: relative;
