        margin-bottom: 0;
        font-size: $big-fz; }

    &__wobbler {
        display: inline-block; }

    &__img-container {
        position: relative;
        margin-top: 70px;
        right: -70px; }

    &__img {
        display: block;
        width: 740px;
        height: 590px;
        margin-left: auto;
        position: relative;
        z-index: 2; } }

.sp-birthday-section-head {
