            margin: 0 auto 30px;
            background-repeat: no-repeat;
            background-size: contain; }

        &[data-icon="star"]::before {
            width: 50px;
            height: 50px;
            background-image: url(../images/special-projects/birthday/svg/star.svg); }

        &[data-icon="friends"]::before {
            width: 74px;
            height: 52px;
