            height: 65px;
            background-image: url(../images/special-projects/birthday/svg/smile.svg); } }


    &__description {
        margin-top: 20px;
        margin-left: auto;
        margin-right: auto;
        font-size: 18px;

        &_1 {
            width: 550px; }

        &_2 {
            width: 810px; }

        &_3 {
            width: 760px; }

        &_4 {
            width: 860px; }

        &_5 {
            width: 760px; }

        &_6 {
            width: 760px; } } }

