.sp-birthday-event {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    width: 310px;
    height: 420px;
    flex-shrink: 0;
    margin-right: 20px;
    margin-bottom: 25px;
    padding: 20px;
    border-radius: 8px;
    overflow: hidden;
    text-decoration: none;

    &::after {
        content: "";
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 2;
        border-radius: 8px;
        background: linear-gradient(rgba(0, 115, 255, 0.02) 0%, rgba(0, 115, 255, 0.02) 50%, rgba(0, 115, 255, 0.6) 75%, rgba(0, 115, 255, 0.95) 100%); }

    &:hover,
    &:focus {
        &::after {
            background: linear-gradient(rgba(0, 115, 255, 0.02) 0%, rgba(0, 115, 255, 0.02) 15%, rgba(0, 115, 255, 0.6) 75%, rgba(0, 115, 255, 0.95) 100%); }

        .sp-birthday-event__link {
            text-decoration: underline; } }

    &__cover {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        border-radius: 8px; }

    &__counter {
        position: absolute;
        top: 20px;
        left: 20px;
        z-index: 3;
        width: 29px;
        height: 33px;
        font-size: 24px;
        color: $base-c;
        text-align: center;
        background-color: $light-c;
        border-radius: 6px;

        &::after {
            content: "";
            display: block;
            width: 26px;
            height: 32px;
            position: absolute;
            right: -6px;
            bottom: -4px;
            background-image: url(../images/special-projects/birthday/svg/counter-shadow.svg); } }

    &__name {
        height: 120px;
        font-size: 32px;
        line-height: $micro-lh;
        font-family: $bold-f;
        color: $light-c;
        position: relative;
        z-index: 3; }

    &__link {
        position: relative;
        z-index: 3;
        font-size: 20px;
        font-family: $add-sb-it-f;
        color: $light-c;
        text-decoration: none; } }

.sp-birthday-section-people {
