    padding-bottom: 100px;

    &::before,
    &::after {
        content: "";
        position: absolute;
        z-index: 1;
        background-repeat: no-repeat;
        background-size: contain; }

    &::before {
        width: 260px;
        height: 700px;
        top: -40px;
        left: -170px;
        background-image: url(../images/special-projects/birthday/svg/reviews-left.svg); }

    &::after {
