.wave-section {
    position: relative;
    background-color: #FAFBFC;
    overflow: hidden; }

.wave-top {
    width: 100%;
    min-width: 1920px;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%); }

.wave-bottom {
    width: 100%;
    min-width: 1920px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%); }

.wave_reverse {
    transform: translateX(-50%) scale(-1, 1); }

.pre-wave-section_first {
    position: relative;
    z-index: 2; }

.wave-section_first {
    z-index: 1;
    margin-top: -200px;
    padding-top: 200px;
    padding-bottom: 50px; }
