.container {
    position: relative;
    margin-right: auto;
    margin-left: auto;

    @include media(m) {
        width: 100%;
        max-width: 600px;
        padding-left: 16px;
        padding-right: 16px; }

    @include media(d) {
        width: 1300px; } }

.layout-section {
    margin-top: 40px; }

.base-layout {
    @include media(m) {
        .base-layout__aside {
            margin-top: 20px; } }

    @include media(d) {
        display: flex;
        align-items: flex-start;

        &__main {
            width: 860px;
            flex-shrink: 0;
            margin-right: 20px; }

        &__aside {
            width: 420px;
            margin-left: auto;
            padding-left: 40px;
            flex-shrink: 0; } } }

.base-layout__aside_desktop {
    @include media(m) {
        display: none; } }

.base-layout__aside_mobile {
    @include media(m) {
        margin-top: 40px; }

    @include media(d) {
        display: none; } }

.aside-widget {
    &:not(:last-child) {
        margin-bottom: 20px; } }

.light-bg {
    background-color: #F5F6F7;
    padding-bottom: 40px; }

.light-bg + .layout-section,
.light-bg + footer {
    margin-top: 0; }

.base-3items-grid {
    @include media(m) {
        & > *:not(:last-child) {
            margin-bottom: 20px; } }

    @include media(d) {
        display: grid;
        grid-template-columns: 310px 310px 640px;
        grid-gap: 20px;
        margin-top: 20px; } }

.base-3items-grid-inverse {
    @include media(m) {
        & > *:not(:last-child) {
            margin-bottom: 20px; } }

    @include media(d) {
        display: grid;
        grid-template-columns: 640px 310px 310px;
        grid-gap: 20px;
        margin-top: 20px; } }

.base-4items-grid {
    @include media(m) {
        & > *:not(:last-child) {
            margin-bottom: 20px; } }

    @include media(d) {
        display: grid;
        grid-template-columns: repeat(4, 310px);
        grid-gap: 20px;
        margin-top: 20px; } }


.base-layout + .items-grid,
.items-grid + .items-grid {
    @include media(m) {
        margin-top: 20px; } }

.mobile-hidden {
    @include media(m) {
        display: none !important; } }

.desktop-hidden {
    @include media(d) {
        display: none !important; } }
