.additional-block {
    padding: 40px 0;
    background-color: $base-dark-c;

    &__title {
        font-size: $big-fz;
        font-family: $bold-f;
        color: $light-c;
        text-transform: uppercase;

        @include media(m) {
            margin-bottom: 30px;
            text-align: center; } } }

.additional-block {
    .additional-block__title {
        letter-spacing: 0.1em; }

    .grid-publication {
        @include media(m) {
            background-color: transparent;
            color: $light-c;

            &:not(:last-child) {
                margin-bottom: 20px;
                padding-bottom: 20px;
                border-bottom: 1px solid #D7DBE0; } }

        @include media(d) {
            border: 1px solid #B1C1D2; } } }

.movie-posters-widget {
    &__title-wrap {
        margin-bottom: 40px;

        @include media(d) {
            display: flex;
            align-items: center; }

        .additional-block__title {
            letter-spacing: 0.02em; } }

    &__btns {
       font-size: 0;

       @include media(m) {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center; }

       @include media(d) {
            margin-left: 50px; } }

    &__btn {
        @include media(m) {
            margin-left: 8px;
            margin-right: 8px; }

        @include media(d) {
            margin-right: 15px; } }

    .slick-list {
        @include media(d) {
            margin-left: -25px;
            margin-right: -25px; } }

    .movie-posters-item_inner {
        @include media(m) {
            position: relative;
            margin-left: auto;
            margin-right: auto;
            width: 220px; } }

    .movie-posters-item__cover {
        width: 100%;
        height: 330px;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2); }

    .slick-prev {
        left: -26px; }

    .slick-next {
        right: -26px; } }

.movie-posters-item {
    position: relative;
    text-decoration: none;

    @include media(d) {
        margin-left: 25px;
        margin-right: 25px; }

    &__cover {
        display: block;
        margin-bottom: 15px;
        border-radius: 10px; }

    &__title {
        font-size: $base-fz;
        font-family: $base-f;
        font-weight: bold;
        letter-spacing: 0.02em;
        color: $light-c; }

    &__genre {
        font-family: $base-f;
        font-size: $mini-fz;
        letter-spacing: 0.02em;
        color: #7a8ba1; }

    &__icons-wrap {
        display: flex;
        align-items: center;
        position: absolute;
        top: 10px;
        right: 15px; }

    &__icon {
        display: block;
        margin-left: 10px;
        color: $light-c; } }
