.footer__instagram-banner-link {
    @include media(m) {
        display: none; }

    @include media(d) {
        display: block;
        width: 880px;
        margin: 0 auto 40px; } }

.footer__instagram-mobile-block {
    @include media(m) {
        display: block;
        margin-bottom: 32px;
        padding: 24px 24px 36px;
        background-color: $base-c;
        color: $light-c;
        border-radius: 16px;
        text-decoration: none;
        text-align: center; }

    @include media(d) {
        display: none; }

    .icon-instagram {
        display: block;
        height: 64px;
        margin: 0 auto 25px;
        font-size: 64px; }

    h3 {
        margin-bottom: 10px;
        font-size: $medium-fz;
        font-family: $bold-f; }

    p {
        font-size: 18px; } }

.aside__instagram-banner-link {
    display: block; }

.instagram-banner {
    display: block;

    &_footer {
        width: 880px;
        height: 120px; }

    &_aside {
        width: 100%;
        max-width: 380px;
        margin-left: auto;
        margin-right: auto; } }

.aside-instagram-widget {
    @include media(d) {
        height: 500px; } }
