.reviews {
    @include media(m) {
        margin: 40px 0; }

    @include media(d) {
        margin: 40px 110px 20px; }

    &__title-wrap {
        display: flex;
        align-items: center;

        @include media(m) {
            padding-bottom: 24px;
            border-bottom: 1px solid #D7DBE0; }

        @include media(d) {
            padding-bottom: 10px;
            border-bottom: 1px solid #7A8BA1; } }

    &__title {
        font-family: $bold-f;
        color: $dark-c;

        @include media(m) {
            margin-right: 10px;
            font-size: 24px; }

        @include media(d) {
            margin-right: 20px;
            font-size: 36px; } }

    &__counter {
        padding-left: 10px;
        padding-right: 10px;
        font-family: $bold-f;
        color: $light-c;
        letter-spacing: 0.02em;
        text-align: center;
        background-color: $base-c;

        @include media(m) {
            min-width: 36px;
            height: 36px;
            line-height: 38px;
            font-size: 24px;
            border-radius: 18px; }

        @include media(d) {
            min-width: 48px;
            height: 48px;
            line-height: 50px;
            font-size: 36px;
            border-radius: 24px; } }

    &__btns-wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include media(m) {
            flex-wrap: wrap;
            justify-content: center; } }

    &__btn {
        @include media(m) {
            margin-top: 32px; }

        @include media(d) {
            margin-top: 40px; } }

    &__more-btn {
        letter-spacing: 0.02em;
        color: $base-c; } }

.reviews-list {
    @include media(m) {
        margin-top: 16px; }

    @include media(d) {
        margin-top: 20px; } }

.review {
    &:not(:last-child) {
        @include media(m) {
            margin-bottom: 32px; }

        @include media(d) {
            margin-bottom: 40px; } }

    &__head {
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        @include media(m) {
            margin-bottom: 8px; }

        @include media(d) {
            margin-bottom: 20px; } }

    &__date {
        margin-right: 20px;
        letter-spacing: 0.08em;
        color: $base-gray;

        @include media(m) {
            margin-bottom: 8px;
            font-size: 12px; }

        @include media(d) {
            font-size: 14px; } }

    &__name {
        font-family: $bold-f;
        letter-spacing: 0.02em;

        @include media(m) {
            margin-bottom: 8px;
            font-size: 12px; }

        @include media(d) {
            font-size: 14px; } }

    &__text {
        line-height: $base-lh;

        @include media(m) {
            font-size: 18px; }

        @include media(d) {
            font-size: 16px; } } }
