.selection-head_page {
    text-align: center;

    @include media(m) {
        margin-top: -8px;
        margin-bottom: 25px; }

    @include media(d) {
        margin-top: 40px;
        margin-bottom: 40px; } }

.selection-head_page_new-year-selection {
    @include media(m) {
        margin-top: -32px; }

    @include media(d) {
        margin-top: -20px; } }


.selection-head_index {
    margin-bottom: 30px;
    text-align: center; }

.selection-head {
    &__subtitle {
        margin-bottom: 15px;
        font-size: $base-fz;
        line-height: $mini-lh;
        font-family: $bold-f;
        color: $base-c;
        letter-spacing: 0.08em;
        text-transform: uppercase;

        &::before {
            display: block;
            margin-bottom: 4px; } }

    &__title {
        margin-bottom: 0;
        letter-spacing: 0.02em;

        @include media(m) {
            font-size: $medium-fz;
            font-weight: normal; }

        @include media(d) {
            font-size: 46px;
            font-family: $bold-f; }

        a {
            color: inherit;
            text-decoration: none;

            &:hover,
            &:focus {
              color: $base-c; } } } }

.index-selection {
    margin-top: 40px;

    @include media(m) {
        padding: 32px 16px;
        border-radius: 8px;
        background: #fff;
        border: 2px solid $base-c;
        box-shadow: 0 20px 24px #e1ecf7; }

    @include media(d) {
        margin-left: -40px;
        margin-right: -40px;
        padding: 40px;
        background-color: $light-c;
        border-radius: 30px;
        box-shadow: 0 12px 28px #e1ecf7; }

    &:not(:last-child) {
        margin-bottom: 40px; }

    &__link-wrap {
        margin-top: 40px;
        text-align: center; }

    &__link {
        display: inline-block; } }

.index-selection-grid {
    @include media(m) {
        & > .grid-publication:not(:last-child) {
            margin-bottom: 20px;
            padding-bottom: 20px;
            border-bottom: 1px solid #C3CEDF; } }

    @include media(d) {
        display: flex;
        justify-content: space-between;

        &_2 .grid-publication {
            width: 640px; }

        &_3 .grid-publication {
            &:nth-child(1),
            &:nth-child(3) {
                width: 310px; }

            &:nth-child(2) {
                width: 640px; } }

        &_4 .grid-publication {
            width: 310px; } } }


.new-year-selection-illustration-wrap {
    overflow: hidden; }

.new-year-selection-illustration {
    display: block;
    width: 1300px;
    max-width: initial;
    margin-bottom: 8px;
    position: relative;
    left: 50%;
    transform: translateX(-50%); }

.selection-head_index_new-year-selection .selection-head__subtitle::before,
.selection-head_page_new-year-selection .selection-head__subtitle::before {
    content: none; }

.selection-head_index_new-year-selection .new-year-selection-illustration-wrap {
    @include media(m) {
        margin-top: -32px;
        margin-left: -16px;
        margin-right: -16px; }

    @include media(d) {
        margin-top: -40px;
        margin-left: -40px;
        margin-right: -40px; } }

