.publication {
    margin-bottom: 50px;

    &__cover-wrap {
        position: relative;
        background-color: $light-gray;

        @include media(m) {
            margin-left: -16px;
            margin-right: -16px; }

        @include media(d) {
            height: 500px; }

        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 2;
            background: linear-gradient(rgba(0, 51, 104, 0) 0%, rgba(8, 44, 86, 0.02) 77.39%, rgba(9, 44, 85, 0.25) 91.63%, rgba(23, 33, 54, 0.8) 100%);
            opacity: .6; } }

    &__cover {
        display: block;
        position: relative;
        z-index: 1; }

    &__img-figcaption {
        position: absolute;
        bottom: 15px;
        left: 30px;
        right: 30px;
        z-index: 3;
        font-size: 0;
        text-align: right; }

    &__img-author {
        display: inline-block;
        font-size: 14px;
        letter-spacing: 0.08em;
        line-height: 20px;
        color: #dde2e5; }

    &__content {
        margin-top: 30px; }

    &__rubric {
        margin-bottom: 20px;
        font-size: 16px;

        &-icon {
            margin-right: 10px;
            color: $base-c;
            vertical-align: middle; } }

    &__date {
        margin-bottom: 20px;
        font-size: 14px;
        letter-spacing: 0.08em;
        line-height: 20px;
        color: $base-gray; }

    &__additional-info-wrap {
        font-size: $mini-fz;

        &_row {
            display: flex;
            align-items: center;

            @include media(m) {
                flex-wrap: wrap; }

            & *:not(:last-child) {
                @include media(m) {
                    margin-right: 20px; }

                @include media(d) {
                    margin-right: 50px; } } }

        [class^="icon-"] {
            display: inline-block;
            width: 24px;
            height: 20px;
            flex-shrink: 0;
            margin-right: 8px;
            font-size: 0;
            line-height: 20px;
            color: $base-c;
            text-align: center;

            &::before {
                font-size: 17px; } }

        .icon-bed {
            vertical-align: 0;

            &::before {
                font-size: 14px; } }

        .icon-star-round {
            vertical-align: -1px;

            &::before {
                font-size: 15px; } }

        a {
            color: $base-c;
            text-decoration: none;
            word-break: break-word;

            &:hover,
            &:focus {
                text-decoration: underline; } } }

    &__event-info {
        display: flex;
        align-items: baseline;
        margin-bottom: 20px; }

    &__place-info-wrap {
        @include media(m) {
            display: block; }

        & * {
            @include media(m) {
                display: block; } }

        &_top {
            margin-bottom: 40px; }

        &_bottom {
            margin-top: 40px;
            margin-bottom: 40px; }

        tr {
            @include media(m) {
                margin-bottom: 16px; }

            td {
                @include media(m) {
                    padding-bottom: 5px; }

                @include media(d) {
                    padding-bottom: 10px; } }

            &:last-child {
                @include media(m) {
                    td:last-child {
                        padding-bottom: 0; } } } }

        td {
            padding-top: 0;
            padding-left: 0;
            padding-right: 0;
            vertical-align: baseline;

            &:first-child {
                @include media(d) {
                    width: 220px;
                    padding-right: 20px; } }

            &:last-child {
                @include media(m) {
                    padding-left: 36px; } } } }

    &__title {
        margin-bottom: 20px;
        font-family: $bold-f;

        @include media(m) {
            font-size: $big-fz; }

        @include media(d) {
            font-size: $huge-fz; } }

    &__lead {
        font-family: $add-sb-it-f;

        @include media(m) {
            margin-bottom: 30px;
            font-size: $base-fz;
            line-height: $base-lh; }

        @include media(d) {
            margin-bottom: 40px;
            font-size: $medium-fz;
            line-height: $mini-lh; } }

    &__footer {
        margin-top: 40px; }

    &__author {
        margin: 0 auto 40px;
        font-size: $base-fz;

        @include media(m) {
            width: 100%; }

        @include media(d) {
            width: 640px; } } }
