.sp-autoclave-col-10 {
    width: 860px;
    margin-left: auto;
    margin-right: auto; }

.sp-autoclave-col-7 {
    width: 750px; }

.sp-autoclave-col-6 {
    width: 640px; }

.sp-autoclave-section {
    &:nth-of-type(1) .sp-autoclave-section-title::before {
        left: calc(-52px - 40px);
        width: 52px;
        height: 74px;
        background-image: url(../images/special-projects/autoclave/svg/1-stroke.svg); }

    &:nth-of-type(2) .sp-autoclave-section-title::before {
        left: calc(-67px - 40px);
        width: 67px;
        height: 74px;
        background-image: url(../images/special-projects/autoclave/svg/2-stroke.svg); }

    &:nth-of-type(3) .sp-autoclave-section-title::before {
        left: calc(-63px - 40px);
        width: 63px;
        height: 74px;
        background-image: url(../images/special-projects/autoclave/svg/3-stroke.svg); }

    &:nth-of-type(4) .sp-autoclave-section-title::before {
        left: calc(-72px - 40px);
        width: 72px;
        height: 74px;
        background-image: url(../images/special-projects/autoclave/svg/4-stroke.svg); }

    &:nth-of-type(5) .sp-autoclave-section-title::before {
        left: calc(-65px - 40px);
        width: 65px;
        height: 74px;
        background-image: url(../images/special-projects/autoclave/svg/5-stroke.svg); }

    &:nth-of-type(6) .sp-autoclave-section-title::before {
        left: calc(-68px - 40px);
        width: 68px;
        height: 74px;
        background-image: url(../images/special-projects/autoclave/svg/6-stroke.svg); } }

.sp-autoclave-section-title {
    position: relative;
    width: 640px;
    margin-bottom: 30px;
    font-size: 36px;
    font-family: $bold-f;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        display: inline-block;
        vertical-align: bottom;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center bottom; } }

.sp-autoclave-h3 {
    font-size: 24px;
    font-family: $bold-f; }

.sp-autoclave-big-text {
    font-size: 26px; }

.sp-autoclave-gap_top_small {
    margin-top: 30px; }

.sp-autoclave-gap_bottom_small {
    margin-bottom: 30px; }

.sp-autoclave-gap_top_medium {
    margin-top: 60px; }

.sp-autoclave-gap_bottom_medium {
    margin-bottom: 60px; }

.sp-autoclave-gap_top_big {
    margin-top: 100px; }

.sp-autoclave-gap_bottom_big {
    margin-bottom: 100px; }

.sp-autoclave-light-bg-section {
    padding-top: 60px;
    padding-bottom: 60px;
    background-color: #FAFBFC; }

.sp-autoclave-to-top-btn {
    position: fixed;
    bottom: 48px;
    right: 48px;
    z-index: 2;
    width: 48px;
    height: 48px;
    padding-right: 2px;
    font-size: 20px;
    line-height: 44px;
    color: $light-c;
    background-color: $base-c;
    border-radius: 50%;
    border: 2px solid $base-c;
    transform: rotate(90deg);

    &:hover,
    &:focus {
        color: $base-c;
        background-color: $light-c; } }

.sp-autoclave- {
    &header {
        position: relative; }

    &header__title {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 2;
        color: $light-c;
        font-size: 124px;
        line-height: 1;
        font-family: $bold-f;
        text-transform: uppercase;

        span {
            display: block;
            margin-top: 15px;
            font-size: 42px;
            white-space: nowrap; } }

    &header__scroll-icon {
        position: absolute;
        left: 50%;
        bottom: 60px;
        transform: translateX(-50%);
        z-index: 2; }

    &wobbler {
        text-align: center; }

    &ore-section {
        position: relative;

        &__text {
            width: 640px; }

        &__img-wrap {
            position: absolute;
            top: 0;
            right: -100px;
            width: 220px; }

        &__img {
            margin-bottom: 20px; }

        &__caption {
            margin-left: 10px;
            margin-right: 10px;
            font-size: 24px;
            line-height: 1.2;
            font-family: $bold-f; } }

    &methods {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 20px;
        margin: 0;
        padding: 0;
        list-style: none; }

    &method {
        padding: 40px 30px;
        border: 2px solid $base-c;
        box-shadow: 0 16px 16px rgba(0, 115, 255, 0.06);
        border-radius: 8px;

        &__number {
            margin-bottom: 30px;
            font-size: 24px;
            line-height: $mini-lh;
            font-family: $add-sb-it-f;
            color: $base-c; }

        &__title {
            margin-bottom: 20px; } }

    &gold-section {
        position: relative;

        &__text {
            width: 640px; }

        &__img-wrap {
            position: absolute;
            top: 0;
            right: -100px;
            width: 200px; }

        &__img {
            width: 175px;
            margin-bottom: 20px; }

        &__caption {
            font-family: $bold-f; }

        &__percent-text {
            display: block;
            margin-bottom: 5px;
            font-size: 40px;
            line-height: 34px;
            color: $base-c; }

        &__percent-description {
            font-size: 24px;
            line-height: 1.2; } }

    &scheme {
        position: relative;

        &_with-bg {
            min-height: 1114px;
            padding-top: 100px;
            padding-bottom: 100px;
            background-image: url(../images/special-projects/autoclave/svg/grid-bg.svg); }

        &__img {
            margin-left: auto;
            margin-right: auto; }

        &__element {
            position: absolute;

            &:hover {
                .sp-autoclave-scheme__short-balloon,
                .sp-autoclave-scheme__long-balloon {
                    opacity: 1;
                    z-index: 1; } } }

        &__btn {
            width: 35px;
            height: 35px;
            flex-shrink: 0;
            font-size: 26px;
            line-height: 36px;
            color: $light-c;
            background-color: $base-c;
            border-radius: 50%; }

        &__text-btn {
            position: relative;
            padding: 4px 10px;
            font-size: 16px;
            line-height: $mini-lh;
            color: $light-c;
            text-transform: uppercase;
            background-color: $base-c;
            border: 2px solid $base-c;
            border-radius: 4px; }

        &__short-balloon {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            z-index: -1;
            padding: 6px 12px;
            font-size: 16px;
            line-height: $mini-lh;
            color: $light-c;
            white-space: nowrap;
            text-transform: uppercase;
            background-color: $base-c;
            opacity: 0;
            transition: opacity $transition-time;

            &_left {
                right: calc(100% + 20px);
                border-radius: 4px 0 0 4px;

                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 0;
                    right: -14px;
                    width: 15px;
                    height: 100%;
                    background-color: $base-c;
                    clip-path: polygon(0% 0%, 25% 0%, 100% 50%, 25% 100%, 0% 100%); } }

            &_right {
                left: calc(100% + 20px);
                border-radius: 0 4px 4px 0;

                &::before {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 0;
                    left: -14px;
                    width: 15px;
                    height: 100%;
                    background-color: $base-c;
                    clip-path: polygon(75% 0%, 100% 0%, 100% 100%, 75% 100%, 0% 50%); } } }

        &__long-balloon {
            position: absolute;
            top: calc(100% + 30px);
            z-index: -1;
            padding: 20px;
            font-size: 18px;
            line-height: $base-lh;
            border: 1px solid $base-c;
            border-radius: 4px;
            background-color: $light-c;
            opacity: 0;
            transition: opacity $transition-time;

            &::before {
                content: "";
                position: absolute;
                left: 20px;
                top: -11px;
                display: block;
                width: 21px;
                height: 21px;
                background-color: $light-c;
                border-top: 1px solid $base-c;
                border-left: 1px solid $base-c;
                transform: rotate(45deg); } } }

    &scheme-1 .sp-autoclave-scheme__element {
        display: flex;
        align-items: center;

        &:nth-child(1) {
            left: 140px;
            bottom: 230px; }
        &:nth-child(2) {
            left: 610px;
            bottom: 450px; }
        &:nth-child(3) {
            left: 600px;
            bottom: 290px; }
        &:nth-child(4) {
            left: 1000px;
            bottom: 550px; }
        &:nth-child(5) {
            right: 140px;
            top: 50px; } }

    &scheme-2 .sp-autoclave-scheme__element {
        &:nth-child(1) {
            left: 75px;
            top: 30px; }
        &:nth-child(2) {
            left: 400px;
            top: 30px; }
        &:nth-child(3) {
            top: 10px;
            left: 820px; }
        &:nth-child(4) {
            left: 210px;
            top: 370px; }
        &:nth-child(5) {
            left: 560px;
            bottom: 520px; }
        &:nth-child(6) {
            left: 90px;
            bottom: 240px; }
        &:nth-child(7) {
            left: 400px;
            bottom: 210px; }
        &:nth-child(8) {
            left: 720px;
            bottom: 190px; }

        &:nth-child(1) .sp-autoclave-scheme__long-balloon {
            width: 280px; }
        &:nth-child(2) .sp-autoclave-scheme__long-balloon {
            width: 270px; }
        &:nth-child(3) .sp-autoclave-scheme__long-balloon {
            width: 330px; }
        &:nth-child(5) .sp-autoclave-scheme__long-balloon {
            width: 410px; }
        &:nth-child(6) .sp-autoclave-scheme__long-balloon {
            width: 310px; }
        &:nth-child(7) .sp-autoclave-scheme__long-balloon {
            width: 170px; }
