    &characteristics-section {
        padding-top: 100px;
        padding-bottom: 60px;
        background-color: #FAFBFC;

        &__scheme {
            width: 1170px;
            margin-left: auto;
            margin-right: auto; }
        &__table-wrap {
            display: flex;

            &_first {
                .sp-autoclave-characteristics-section__table td {
                    padding-bottom: 60px; } } }

        &__table-title {
            width: 310px;
            flex-shrink: 0;
            flex-grow: 0;
            padding-left: 50px;
            padding-right: 25px;
            font-size: $big-fz;
            font-family: $bold-f; }

        &__table {
            height: 100%;
            border-spacing: 0;
            border-collapse: collapse;

            th {
                padding: 0 30px 10px;
                font-size: 16px;
                font-family: $base-f;
                font-weight: normal;
                text-transform: uppercase;
                text-align: left;
                border-left: 1px solid $base-c; }

            td {
                padding: 0 30px 10px;
                width: 200px;
                font-size: 24px;
                font-family: $bold-f;
                border-left: 1px solid $base-c;

                span {
                    font-size: 42px;
                    color: $base-c; } } } }

    &kek-scheme {
        display: flex;
        align-items: center;

        &__circle {
            width: 160px;
            height: 160px;
            border: 2px solid $base-c;
            border-radius: 50%;
            overflow: hidden;

            &_text {
                font-size: 36px;
                line-height: 160px;
                font-family: $bold-f;
                color: $base-c;
                letter-spacing: 2px;
                text-align: center; } }

        &__equal {
            display: block;
            margin-right: 30px;
            margin-left: 30px;
            font-size: 40px;
            line-height: 1;
            font-family: $bold-f;
            color: $base-c; }

        &__img {
            width: 100%;
            height: 100%; } }

    &water-cycle-section {
        &__note {
            margin-top: 30px;
            margin-left: 110px;
            font-size: 14px; }

        &__title {
            text-align: center; }

        &__factoids {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            width: 820px;
            margin: 0 auto;
            padding: 0;

            .sp-autoclave-icon-factoid {
                flex: 0 0 200px; } } }

    &water-cycle-scheme {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        &__part {
            flex: 0 0 420px; }

        &__step {
            display: flex;
            align-items: flex-start;
            font-size: 18px;

            &::before {
                content: "";
                display: block;
                width: 26px;
                height: 26px;
                margin-top: 2px;
                margin-right: 15px;
                flex-shrink: 0;
                background-image: url(../images/special-projects/autoclave/svg/drop.svg);
                background-size: cover;
                background-repeat: no-repeat; }

            &:not(:last-child) {
                margin-bottom: 20px; } }

        &__img {
            width: 310px;
            flex-shrink: 0; } }

    &icon-factoid {
        &_center {
            text-align: center;

            .sp-autoclave-icon-factoid__icon {
                margin-left: auto;
                margin-right: auto; } }

        &__icon {
            margin-bottom: 15px;

            &_big {
                width: 106px;
                height: 106px; }

            &_small {
                width: 64px;
                height: 64px; } }

        &__text {
            font-size: 16px; } }


    &eco-control-section__factoids {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 40px 20px;
        margin: 0;
        padding: 0; }

    &cyanide-section {
        display: flex;
        align-items: flex-start; }

    &cyanide {
        width: 230px;
        flex-shrink: 0;
        margin-left: 130px;
        cursor: pointer;

        &:hover .sp-autoclave-cyanide__caption {
            color: $base-c; }


        &__img {
            margin-bottom: 20px; }

        &__caption {
            font-size: 24px;
            line-height: 1.2;
            font-family: $bold-f;
            transition: color $transition-time; } }

    &map-section {
        margin-bottom: -100px;

        &__title {
            width: 420px;
            margin-left: 220px;
            font-size: 24px;
            line-height: $mini-lh;
            font-family: $bold-f; }

        &__img {
            position: relative;
            top: -140px; } }

    &footer {
        &__title {
            font-size: 42px;
            line-height: 1.2;
            font-family: $bold-f;
            color: $base-c; }

        &__text {
            a {
                color: $base-c;

                &:hover,
                &:focus {
                    text-decoration: none; } } } }

    &video-wrap {
        position: relative; }

    &video-play-btn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
        width: 106px;
        height: 106px;
        border-radius: 50%;
        border: 1px solid $base-c;
        background-color: rgba($light-c, .4);

        &:hover,
        &:focus {
            background-color: $light-c; }

        &::before {
            content: "";
            width: 75px;
            height: 75px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 2;
            border-radius: 50%;
            background-color: $base-c; }

        &::after {
            content: "";
            width: 0;
            height: 0;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 3;
            margin-left: 4px;
            border-top: 18px solid transparent;
            border-left: 32px solid $light-c;
            border-bottom: 18px solid transparent; } }

    &share-widget-wrap {
        height: 0;
        position: sticky;
        margin-right: 54px;
        bottom: 50%;
        transform: translateY(-50%);
        text-align: right; }

    &share-widget {
        display: inline-block;
        position: relative;
        top: -174px;

        .share-widget__link {
            display: block;
            margin-right: 0;

            &:not(:last-child) {
                margin-bottom: 10px; } }

        .share-widget__link-img {
            width: 36px;
            height: 36px; } }

    &navigation-wrap {
        height: 0;
        position: sticky;
        bottom: 50%;
        transform: translateY(-50%);
        margin-left: 32px;
        width: max-content; }

    &navigation {
        position: relative;
        top: -240px;
        margin: 0;
        padding: 0;

        &__btn {
            display: flex;
            align-items: center;
            padding: 10px;
            color: #CBD3DA;

            &:hover {
                .sp-autoclave-navigation__text {
                    max-width: 100%;
                    margin-right: 15px; } }

            &_current {
                .sp-autoclave-navigation__indicator {
                    &::after {
                        transform: scale(1); } } } }

        &__text {
            display: inline-block;
            max-width: 0;
            margin-right: 0;
            flex-grow: 1;
            font-size: 16px;
            white-space: nowrap;
            overflow: hidden;
            transition: max-width .2s ease-in-out, margin-right .2s ease-in-out;

            &:first-letter {
                font-family: $bold-f; } }

        &__indicator {
            position: relative;
            display: inline-block;
            width: 6px;
            height: 6px;
            background-color: #CBD3DA;
            border-radius: 50%;

            &::after {
                content: "";
                position: absolute;
                left: -5px;
                top: -5px;
                width: 16px;
                height: 16px;
                border-radius: 50%;
                border: 1px solid #CBD3DA;
                transform: scale(0);
                transition: transform .2s ease-in-out; } } } }


.sp-autoclave-pop-up-body-cover {
    display: block;
    min-width: 100%;
    z-index: -1;
    visibility: hidden;
    overflow: auto hidden;

    &.visible {
        visibility: visible;
        z-index: 100; } }

.sp-autoclave-pop-up-wrapper {
    display: block;
    position: absolute;
    width: 100%;
    visibility: hidden; }

.sp-autoclave-pop-up-wrapper.visible {
    height: 100%;
    justify-content: initial;
    visibility: visible;

    .sp-autoclave-pop-up-container {
        transform: translateX(0); }

    .sp-autoclave-pop-up-back {
        opacity: 1; } }

.sp-autoclave-pop-up-back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background-color: rgba(#7F8DA4, .9);
    opacity: 0;
    cursor: pointer;
    transition: opacity ease-in $transition-time;

    @supports (backdrop-filter: none) {
        background-color: transparent;
        backdrop-filter: blur(7px); } }

.sp-autoclave-pop-up-close {
    position: sticky;
    display: block;
    width: 32px;
    height: 32px;
    margin-left: auto;
    font-size: 32px;
    top: 32px;
    right: 32px;
    color: $dark-c; }

.sp-autoclave-pop-up-container {
    position: relative;
    width: calc((100vw - 1300px) / 2 + 970px);
    min-width: 990px;
    background-color: $light-c;
    overflow: auto;
    box-shadow: 0 26px 28px #2e5194;
    transform: translateX(-100vw);
    transition: transform ease-in $transition-time;
    will-change: left; }

.sp-autoclave-pop-up-content {
    width: 970px;
    margin-top: -40px;
    margin-left: auto;
    padding: 70px 220px 70px 110px; }

.sp-autoclave-pop-up__title {
    width: 470px;
    margin-bottom: 60px;
    font-size: 36px;
    line-height: $mini-lh;
    font-family: $bold-f; }

.sp-autoclave-pop-up__text {
    width: 760px;
    font-size: 18px;
    line-height: $base-lh;

    & + .sp-autoclave-pop-up__text {
        margin-top: 20px; } }

.sp-autoclave-pop-up__img {
    margin-top: 60px;
    margin-bottom: 20px; }
