.mobile-menu-open .header {
    @include media(m) {
        position: fixed;
        top: 0;
        width: 100%;
        animation: none; } }

.header {
    position: relative;
    z-index: 999;
    background-color: $light-c;

    @include media(m) {
        height: 70px;
        box-shadow: 0 1px 5px #d7e2ee; }

    @include media(d) {
        height: 100px;
        border-bottom: 1px solid #D7DBE0; } }

.header_no-shadow {
    box-shadow: none; }

.header_mobile-unfixed {
    @include media(m) {
        position: sticky;
        top: 0;
        animation: slideUp $transition-time ease-out forwards;
        box-shadow: none; } }

.header_mobile-fixed {
    @include media(m) {
        position: sticky;
        top: 0;
        box-shadow: 0 1px 5px #d7e2ee;

        &.animate {
            animation: slideDown $transition-time ease-out forwards; } } }

@keyframes slideDown {
    0% {
        top: -70px; }

    100% {
        top: 0; } }

@keyframes slideUp {
    0% {
        top: 0; }

    100% {
        top: -70px; } }

.header-container {
    height: 100%;
    display: flex;
    align-items: center; }

.header {
    &__logo-link {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        position: relative;
        z-index: 2;
        text-decoration: none;

        @include media(m) {
            height: 45px;
            align-self: flex-start;
            margin-top: 5px; }

        @include media(d) {
            height: 67px;
            margin-bottom: 24px; } }

    &__logo-subtext {
        margin-top: 6px;
        font-size: 11px;
        line-height: $micro-lh;
        letter-spacing: 0.07em;
        color: #242434;

        @include media(m) {
            display: none; } }

    &__logo-img {
        display: block;
        width: 147px; }

    &__logo-icon {
        position: absolute;
        color: $base-c;
        font-size: 0;

        &.icon-logo-sign {
            right: 19px;
            &::before {
                font-size: 8px; }

            @include media(m) {
                bottom: 20px; }

            @include media(d) {
                bottom: 40px; } }

        &.icon-logo-news {
            @include media(m) {
                right: 22px;
                bottom: 24px;

                &::before {
                    font-size: 17px; } }

            @include media(d) {
                right: 19px;
                bottom: 40px;

                &::before {
                    font-size: 19px; } } }

        &.icon-logo-event {
            right: 26px;

            @include media(m) {
                bottom: 22px;

                &::before {
                    font-size: 19px; } }

            @include media(d) {
                bottom: 37px;

                &::before {
                    font-size: 21px; } } }

        &.icon-logo-history {
            @include media(m) {
                bottom: 21px;
                right: 17px;

                &::before {
                    font-size: 16px; } }

            @include media(d) {
                bottom: 37px;
                right: 16px;
                &::before {
                    font-size: 18px; } } }

        &.icon-geo-stroke {
            @include media(m) {
                bottom: 19px;
                right: 19px;

                &::before {
                    font-size: 22px; } }

            @include media(d) {
                bottom: 35px;
                right: 19px;

                &::before {
                    font-size: 24px; } } }

        &.icon-logo-people {
            @include media(m) {
                bottom: 22px;
                right: 18px;

                &::before {
                    font-size: 22px; } }

            @include media(d) {
                bottom: 35px;
                right: 17px;

                &::before {
                    font-size: 25px; } } }

        &.icon-star-sharp {
            @include media(m) {
                bottom: 22px;
                right: 10px;

                &::before {
                    font-size: 21px; } }

            @include media(d) {
                bottom: 37px;
                right: 23px;

                &::before {
                    font-size: 22px; } } } }

    &__nav-wrap {
        margin-left: auto;
        margin-right: 30px;

        @include media(m) {
            display: none;
            position: fixed;
            top: 70px;
            left: 0;
            z-index: 999;
            width: 100%;
            height: calc(100% - 70px);
            background-color: $light-c;
            overflow: auto;

            &_inner {
                display: flex;
                flex-direction: column;
                width: 100%;
                max-width: 600px;
                height: 100%;
                margin-left: auto;
                margin-right: auto;
                padding-left: 16px;
                padding-right: 16px; }

            &.visible {
                display: block; } } }

    &__search-btn {
        display: block;
        width: 42px;
        height: 42px;
        color: $dark-c;

        @include media(m) {
            margin-left: auto;
            font-size: 22px; }

        @include media(d) {
            font-size: $base-fz; } }

    &__menu-toggler {
        width: 42px;
        height: 42px;
        margin-left: 8px;
        font-size: 18px;

        @include media(d) {
            display: none; } }

    &__weather-widget {
        position: absolute;
        top: 40px;
        left: 490px;
        font-size: $mini-fz;
        font-family: $bold-f;
        color: $dark-c;

        @include media(m) {
            display: none; } }

    &__pattern {
        @include media(m) {
            &_desktop {
                display: none; } }

        @include media(d) {
            position: absolute;
            width: 540px;
            left: 0;
            bottom: 10px;
            z-index: 1; } }

    &__social-wrap {
        margin-bottom: 25px;

        .social-link {
            color: $light-c;
            background-color: $base-c; } }

    &__mobile-actions {
        & > * {
            display: block;
            margin-top: 20px;
            color: #7A8BA1;
            font-size: 16px;
            text-decoration: underline; } }

    &__mobile-copyright {
        font-size: 14px;
        text-decoration: none; } }

.header-mobile-nav__bottom-section {
    margin-top: auto;

    &::after {
        content: "";
        display: block;
        height: 80px;
        margin-top: 10px;
        margin-bottom: 20px;
        background-image: url(../images/svg/header-pattern-mobile.svg);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center; }

    @include media(d) {
        display: none; } }
