.index-manage-row {
    @include media(m) {
        & + .index-manage-row {
            margin-top: 20px; }

        & > .grid-publication:not(:last-child) {
            margin-bottom: 20px; } }

    @include media(d) {
        display: flex;
        justify-content: space-between;

        & > .grid-publication {
            &:not(.grid-publication_6-col) {
                flex-basis: 310px; } }

        &:first-child {
            margin-bottom: 20px; } } }

.todo-layout {
    @include media(m) {
        & > .grid-publication:not(:last-child) {
            margin-bottom: 20px; } }

    @include media(d) {
        display: grid;
        grid-template-columns: repeat(4, 310px);
        grid-gap: 20px;

        & > .grid-publication_6-col {
            grid-column: 1/3;
            grid-row: 1/2; }

        & > .cinema-widget {
            grid-column: 4/5;
            grid-row: 1/2; } } }
