.grid-publication-placeholder {
    background-color: $light-c;
    @extend %border;

    &_top1 {
        height: 500px; }

    &_top-other {
        min-height: 460px; } }

.grid-publication {
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: $light-gray;
    text-decoration: none;
    color: inherit;
    @extend %border;

    @include media(d) {
        min-height: 460px; }

    a {
        text-decoration: none;
        color: inherit; }

    & > a:not(.grid-publication__link) {
        position: relative;
        display: flex;
        flex-direction: column;
        height: 100%;
        flex-grow: 1; }


    &__cover {
        position: relative;
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center; }

    &__text-wrap {
        position: relative;
        z-index: 3;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        background-color: $light-c;

        @include media(m) {
            padding: 20px 15px; }

        @include media(d) {
            padding: 20px; } }

    &__title {
        margin-bottom: 5px;
        font-family: $bold-f;

        @include media(m) {
            font-size: 21px; }

        @include media(d) {
            font-size: $medium-fz; }

        a {
            color: currentColor;
            text-decoration: none; } }

    &__description,
    &__date {
        margin-bottom: 20px; }

    &__text {
        font-size: $mini-fz;
        line-height: $base-lh; }

    &__wobbler {
        display: flex;
        align-items: center;
        margin-top: auto; }

    &__link {
        position: absolute;
        bottom: 20px;
        z-index: 4;
        height: 26px;
        font-size: 12px;
        line-height: 22px;
        font-family: $base-f;
        font-weight: bold;
        letter-spacing: 0.08em;
        text-decoration: none;
        transition: color $transition-time;

        @include media(m) {
            margin-left: 10px; }

        @include media(d) {
            margin-left: 20px; }

        &_reportage {
            left: 160px; }

        &_event {
            left: 145px; }

        &:hover {
            color: $base-c;

            &::before {
                width: calc(100% + 14px); } }

        span {
            position: relative;
            z-index: 2; }

        .text {
            padding-left: 2px;
            vertical-align: -3px; }

        .icon {
            margin-left: 10px;
            font-size: 10px;
            color: $base-c;
            vertical-align: -4px; }

        &::before {
            content: "";
            display: block;
            position: absolute;
            top: 1px;
            right: -7px;
            z-index: 1;
            width: 26px;
            height: 26px;
            border-radius: 26px;
            background-color: #EFF2F5;
            transition: width $transition-time, border-radius $transition-time; } }

    &__bg-gradient {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        background-color: rgba(#4c607b, .1); }

    &_6-col {
        @include media(d) {
            width: 640px;
            flex-shrink: 0; } }

    &_8-col {
        @include media(d) {
            width: 860px;
            height: 500px;
            flex-shrink: 0; }

        .grid-publication__text-wrap {
            @include media(d) {
                position: absolute;
                bottom: 0;
                width: 100%; } } }

    &_6-col,
    &_8-col {
        &.grid-publication_format-full {
            @include media(d) {
                .grid-publication__title {
                    font-size: 36px; }

                .grid-publication__text-wrap {
                    background-color: transparent; }

                .grid-publication__text {
                    font-size: 20px; }

                .grid-publication__description,
                .grid-publication__date {
                    margin-bottom: 40px; } } } }

    &_format-base {
        .grid-publication {
            &__cover-wrap {
                position: relative;
                @include media(m) {
                    padding-bottom: 76%; }

                @include media(d) {
                    height: 278px; } }

            &__cover {
                border-bottom: 1px solid $border-c;

                @include media(m) {
                    position: absolute; } }

            &__title {
                &:hover,
                &:focus {
                    color: $base-c; } }

            &__link {
                color: $base-c; } } }

    &_format-full {
        color: $light-c;

        .grid-publication {
            &__cover-wrap {
                position: absolute;
                height: 100%;

                &::after {
                    content: "";
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: linear-gradient(rgba(0, 51, 104, 0) 0%, rgba(23, 33, 54, 0.6) 100%); } }

            &__text-wrap {
                background-color: transparent;

                @include media(m) {
                    margin-top: 220px; }

                @include media(d) {
                    margin-top: 278px; } }

            &__link {
                color: $light-c;

                &:hover {
                    color: $base-c; } } } }

    &_format-city-guide {
        background-color: $base-dark-c;
        color: $light-c;

        .grid-publication__cover-wrap {
            height: 280px;
            background-color: $base-dark-c; }

        .grid-publication__cover {
            width: 260px;
            height: 220px;
            margin: 50px auto 0; }

        .grid-publication__text-wrap {
            background-color: transparent; } }

    &_format-mobile {
        @include media(m) {
            min-height: 0;
            padding: 0;
            color: $dark-c;
            border: none;
            border-radius: 0;
            background-color: transparent;

            .grid-publication__cover-wrap {
                display: none; }

            .grid-publication__text-wrap {
                margin-top: 0;
                padding: 0;
                background-color: transparent; } } } }


.index-page .grid-publication_hide-cover {
    @include media(m) {
        min-height: 0;
        height: auto;
        color: $dark-c;

        .grid-publication__cover-wrap {
            display: none; }

        .grid-publication__text-wrap {
            margin-top: 0;
            background-color: $light-c; } } }

.grid-publication:not(.grid-publication_format-city-guide) {
    &:hover,
    &:focus {
        .grid-publication__bg-gradient {
            display: block; } } }

.grid-publication_format-city-guide {
    &:hover,
    &:focus {
        .grid-publication__title {
            color: $base-c; } } }
